<template>
  <div>
    <p>动态组件切换 和 使用keep-alive维持组件状态</p>
    <button @click="handler(1)">组件1</button>
    <button @click="handler(2)">组件2</button>

    <div class="box-wrapper">
      <!-- 动态组件 和 在 动态组件上使用 keep-alive 来维持组件的状态 -->
      <keep-alive>
        <component :is="current"></component>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import Comone from "@/components/Comone.vue";
import Comtwo from "@/components/Comtwo.vue";
export default {
  components: {
    Comone,
    Comtwo,
  },
  data() {
    return {
      current: "Comone",
    };
  },
  methods: {
    handler(val) {
      if (val === 1) {
        this.current = "Comone";
      } else if (val === 2) {
        this.current = "Comtwo";
      }
    },
  },
};
</script>

